import messages from './messages';
import validator from 'validator';

export default class Contact{
    constructor(classForm) {
        this.form = document.querySelector(classForm);
    };

    init(){
        this.events();
    };

    events(){
        if(!this.form) return;
        this.form.addEventListener('submit', (e) => {
            e.preventDefault();
            this.validate(e);
        });
    };

    validate(e){
        const el = e.target;
        const nameInput = el.querySelector('input[name="name"]');
        const surnameInput = el.querySelector('input[name="surname"]');
        const emailInput = el.querySelector('input[name="email"]');
        const telphoneInput = el.querySelector('input[name="telphone"]');
        let error = false;

        messages.clearMessages(el);

        console.log(emailInput.value);

        if(nameInput.value === '') {
            messages.messageError('Nome  um campo obrigatrio', el);
            error = true;
        }

        if(emailInput.value !== ''){
            const isEmail = validator.isEmail(emailInput.value);

            if(!isEmail) {
                messages.messageError('E-mail Invlido', el);
                error = true;
            }
        }

        

        if(!error) el.submit();
    }
};